@import url('https://fonts.googleapis.com/css?family=Acme|Open+Sans');

header {
  font-family: 'Acme',sans-serif;
  padding: 0;
  margin: 0;
  background-color: #2685cf;
  color: #fefefe;
  height: 65px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

header > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 0;
  margin-right: 20px;
}

header > div > div {
  margin: 0px 8px;
  width: 1px;
  background-color: #3d70bc;
}

p {
  font-family: 'open-sans', sans-serif;
}

img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fefefe;
  margin-right: 10px;
}

header > h1 {
  margin-left: 30px;
}

.link {
  cursor: pointer;
}



